#darkMode {
	background: transparent;
	background-size: 30px 30px;
	height: 45px;
	width: 20px;
	filter: grayscale(100%);
	border: none;
	border-radius: 50%;
	transition: background-color 0.3s ease-in-out, filter 0.3s ease-in-out;
}

#darkMode:hover,
#darkMode:focus {
	filter: none;
	background: transparent;
	cursor: pointer;
}

#darkMode.clicked {
	filter: none !important;
}